<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>

      <div class="right">
        <div class="right-content">
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>活动管理</span>
            </p>
          </div>

          <!-- 操作 -->
          <div class="operate">
            <el-button
              type="primary"
              icon="el-icon-success"
              class="sreach-btn"
              @click="operate(1)"
            >批量上架</el-button>
            <el-button
              type="primary"
              icon="el-icon-remove"
              class="sreach-btn"
              @click="operate(0)"
            >批量下架</el-button>
            <el-button type="primary" icon="el-icon-success" class="sreach-btn" @click="delAll">批量删除</el-button>
            <el-button
              type="primary"
              icon="el-icon-circle-plus"
              class="sreach-btn"
              @click="newsAdd"
            >增加</el-button>
          </div>
          <!-- 产品列表 -->

          <el-table
            class="pro-table"
            :data="tableData"
            border
            style="width: 100%"
            ref="poductTable"
            @selection-change="selectChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="id" label="编号" width="80"></el-table-column>
            <el-table-column label="活动名称">
              <template slot-scope="scope">
                <a
                  :href="'http://localhost/html/courseInfo.html?id='+tableData[scope.$index].details_id"
                >{{tableData[scope.$index].name}}</a>
              </template>
            </el-table-column>
            <el-table-column prop="state" sortable label="状态" width="120"></el-table-column>
            <el-table-column prop="time" sortable label="时间" width="120"></el-table-column>
            <el-table-column label="图片" width="100">
              <template slot-scope="scope">
                <el-popover placement="top-start" title trigger="hover">
                  <img
                    :src="RespondURL.url+tableData[scope.$index].img"
                    alt
                    class="patent-img"
                    style="width: 250px;height: 250px"
                  />
                  <img
                    slot="reference"
                    :src="RespondURL.url+tableData[scope.$index].img"
                    style="width: 50px;height: 50px"
                  />
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <i class="el-icon-edit-outline" title="编辑" @click="edit(scope.$index)"></i>
                <i class="el-icon-success" title="上架" @click="up(scope.$index)"></i>
                <i class="el-icon-remove" title="下架" @click="down(scope.$index)"></i>
                <i class="el-icon-delete" title="删除" @click="del(scope.$index)"></i>
                <i class="el-icon-folder-add" title="加入轮播" @click="addLunBo(scope.$index)"></i>
              </template>
            </el-table-column>
          </el-table>

          <!-- 分页 -->
          <div class="page">
            <el-pagination
              layout="prev, pager, next"
              :total="total"
              @current-change="pageChange"
              @prev-click="pageChange"
              @next-click="pageChange"
              background
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "Activity",
  components: { Left, HeaderContent },
  created() {
    this.getList(1);
  },
  data() {
    return {
      curid: "4",
      stateArr: ["下架", "上架"], // 状态
      total: 0, // 总个数
      tableData: [], //列表数据
      checked: [], //被选中的数据
      isPull: false
    };
  },
  methods: {
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    // 加入轮播
    addLunBo(index) {
      let state = "";
      this.stateArr.forEach((val, num) => {
        if (val == this.tableData[index].state) {
          state = num;
        }
      });
      let obj = {
        name: this.tableData[index].name,
        classify: "活动",
        state: state,
        details_id: this.tableData[index].details_id,
        fatherid: this.tableData[index].id,
        img: this.tableData[index].img
      };
      this.$ajax
        .post(this.RespondURL.url + "Slideshow/insertslideshow", obj)
        .then(data => {
          if (!data.data.flag) {
            this.$alert("已加入轮播图,请选择其他", "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          } else {
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          }
        });
    },
    // 获得列表
    getList(sum) {
      this.$ajax
        .get(this.RespondURL.url + "HonorsAndPatents/findactivity/" + sum)
        .then(data => {
          data.data.result.list.forEach(val => {
            let num = parseInt(val.state);
            val.state = this.stateArr[num];
          });
          this.tableData = data.data.result.list;
          this.total = data.data.result.total;
        });
    },
    newsAdd() {
      this.$router.push("/activityAdd");
    },
    // 选中
    selectChange(data) {
      this.checked = data;
    },
    //编辑
    edit(index) {
      this.$router.push({
        path: "/activityAdd",
        query: {
          id: this.tableData[index].details_id
        }
      });
    },
    // 上架
    up(index) {
      let obj = {
        id: [this.tableData[index].id]
      };
      this.$ajax
        .post(this.RespondURL.url + "ActivityContent/putaway", obj)
        .then(data => {
          if (data.data.flag) {
            this.tableData[index].state = "上架";
          }
        });
    },
    // 下架
    down(index) {
      let obj = {
        id: [this.tableData[index].id]
      };
      this.$ajax
        .post(this.RespondURL.url + "ActivityContent/soldout", obj)
        .then(data => {
          if (data.data.flag) {
            this.tableData[index].state = "下架";
          }
        });
    },
    // 批量操作
    operate(index) {
      if (this.checked.length == 0) {
        return;
      }
      let idArr = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
      });
      let obj = {
        id: idArr
      };
      let repURL = "";
      if (index == 1) {
        repURL = this.RespondURL.url + "ActivityContent/putaway";
      } else {
        repURL = this.RespondURL.url + "ActivityContent/soldout";
      }
      this.$ajax.post(repURL, obj).then(data => {
        if (data.data.flag) {
          this.$alert(data.data.describe, "提示", {
            confirmButtonText: "确定",
            callback: () => {
              this.tableData.forEach(val => {
                this.checked.forEach(e => {
                  if (e.id == val.id) {
                    val.state = this.stateArr[index];
                  }
                });
              });
              //选中清除
              this.checked = [];
              let refsElTable = this.$refs.poductTable;
              refsElTable.clearSelection();
            }
          });
        }
      });
    },
    // 删除
    del(index) {
      let obj = {
        id: [this.tableData[index].id],
        details_id: [this.tableData[index].details_id]
      };
      let img = {
        fileurl: [this.tableData[index].img]
      };
      this.$confirm("此操作将删除这些活动, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$ajax
            .post(this.RespondURL.url + "HonorsAndPatents/batchdelete", obj)
            .then(data => {
              if (data.data.flag) {
                this.tableData.splice(index, 1);
              }
            });
          this.$ajax
            .post(this.RespondURL.url + "setmeal/deletefile", img)
            .then(data => {
              if (!data.data.flag) {
                console.log(data);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 批量删除
    delAll() {
      if (this.checked.length == 0) {
        return;
      }
      let arr = this.tableData.map(x => x);
      let idArr = [];
      let idArr2 = [];
      let imgArr = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
        idArr2.push(val.details_id);
        imgArr.push(val.img);
        arr.forEach((value, index) => {
          if (val.id == value.id) {
            arr.splice(index, 1);
          }
        });
      });
      let obj = {
        id: idArr,
        details_id: idArr2
      };
      let imgobj = {
        fileurl: imgArr
      };
      this.$confirm("此操作将删除这些活动, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          //删除活动
          this.$ajax
            .post(this.RespondURL.url + "HonorsAndPatents/batchdelete", obj)
            .then(data => {
              if (data.data.flag) {
                this.tableData = arr;
              }
            });
          //删除图片
          this.$ajax
            .post(this.RespondURL.url + "setmeal/deletefile", imgobj)
            .then(data => {
              if (!data.data.flag) {
                console.log(data);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 分页
    pageChange(data) {
      this.getList(data);
    }
  }
};
</script>
